import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { Project } from '../models/project';

@Injectable({
  providedIn: 'root'
})
export class ViewpService {

  private readonly URL = environment.endpoint + `/project/user`; // URL to web api

  httpOptions = {
    headers: new HttpHeaders({ 'Content-Type': 'application/json' })
  };

  projectList: Project[];

  public listProjects(): Observable<Project[]> {
    return this.http.get<Project[]>(this.URL + '?username=' + localStorage.getItem('username'));
  }

  constructor(private http: HttpClient) {}
}
